.topnav {
  width: 100%;
  padding: 18px 18px 0 18px;
  background: #0f1115;
}

.topnav-inner {
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
}

.topnav-brand {
  font-size: 18px;
  font-weight: 900;
  letter-spacing: 0.2px;
  color: #e9edf2;
  text-align: center;
}

.topnav-actions {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
}

.topnav-button {
  font-weight: 800;
  font-size: 13px;
  border: 1px solid rgba(255, 255, 255, 0.12);
  padding: 10px 12px;
  cursor: pointer;
  background: #1a2030;
  color: #e9edf2;
  border-radius: 10px;
}

.topnav-button:hover {
  border-color: rgba(255, 255, 255, 0.22);
  background: #1d2537;
}

.topnav-button.active {
  border-color: rgba(244, 197, 66, 0.35);
  background: rgba(244, 197, 66, 0.14);
}

.topnav-button.primary {
  border-color: rgba(244, 197, 66, 0.35);
  background: rgba(244, 197, 66, 0.14);
}
